import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import * as ethers from 'ethers/index.js';
import Config from '../../../contract/config.json';
import { Event } from '../../../contract/index';
import dayjs from 'dayjs';

@inject('ethereumStore')
@observer
export default class WinOrLost extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rate: '',
            devRate: '',
            difficult: '',
            gameStarted: '',
            gameStartTime: '',
            probability: '',
            balance: '',
            openHistory: false,
            history: [],
        }
        this.provider = this.props.ethereumStore.provider;
        this.contract = new ethers.Contract(Config.WinOrLose[3].contractAddress, Config.WinOrLose.abi, this.provider)
    }
    componentWillMount = () => {
        this.getInfo()
        this.interval = setInterval(() => {
            this.getInfo();
            this.state.openHistory && this.getHistory()
        }, 4000)
    }
    componentWillUnmount = () => {
        clearInterval(this.interval)
    }

    getInfo = async () => {
        let getStatus = await this.contract.getStatus();

        let [rate, devRate, difficult, gameStarted, gameStartTime, probability, balance] = getStatus;
        [rate, devRate, difficult, gameStarted, gameStartTime, probability, balance] = JSON.parse(JSON.stringify([rate, devRate, difficult, gameStarted, gameStartTime, probability, balance]));
        balance = ethers.utils.formatEther(balance)
        this.setState({
            rate, devRate, difficult, gameStarted, gameStartTime, probability, balance
        })
    }

    play = async () => {
        let signer = this.contract.connect(this.props.ethereumStore.wallet.connect(this.provider));
        let tx = await signer.play({ value: ethers.utils.parseEther(this.state.eth) });
        alert('txid: ' + tx.hash);
        await tx.wait();
        this.getInfo();
    }

    getHistoryReact = () => {
        console.log(this.state.history)
        let rows = this.state.history.map((v, i) => {
            let { timestamp, number, winner, forWiner, forOwner, eth, player, transactionHash } = v
            return (
                <tr key={transactionHash}>
                    <td>{i + 1}</td>
                    <td>{player}</td>
                    <td>{eth}</td>
                    <td>{forWiner ? '中獎了！' : '沒中'}</td>
                    <td>{forWiner || '銘謝惠顧'}</td>
                    <td>{timestamp}</td>
                </tr>
            )
        })
        return (
            <table className="table table-bordered">
                <thead>
                    <tr>
                        <th>#</th>
                        <th>玩家</th>
                        <th>投注(ETH)</th>
                        <th>中/沒中</th>
                        <th>中多少(ETH)</th>
                        <th>時間</th>
                    </tr>
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </table>
        )
    }


    getHistory = async () => {
        let Lottery = Event.bind(this.provider)(this.contract, 'Lottery')
        let Winning = Event.bind(this.provider)(this.contract, 'Winning')
        Lottery = await Lottery;
        Winning = await Winning;
        Lottery = Lottery.map(v => {
            let [timestamp, number, player, eth] = v
            return { player, eth: ethers.utils.formatEther(eth), number, timestamp: dayjs(timestamp * 1000).format('YYYY年MM月DD日 HH:mm:ss'), transactionHash: v.transactionHash };
        })
        Winning = Winning.map(v => {
            let [timestamp, number, winner, forWiner, forOwner] = v
            console.log(timestamp)
            return { number, winner, forOwner: ethers.utils.formatEther(forOwner), forWiner: ethers.utils.formatEther(forWiner), timestamp: dayjs(timestamp * 1000).format('YYYY年MM月DD日 HH:mm:ss'), transactionHash: v.transactionHash };
        })
        let rows = Lottery.map(v => {
            let winner = Winning.find(x => {
                return x.transactionHash == v.transactionHash
            })
            return Object.assign({}, v, winner);
        })

        rows = rows.reverse()
        this.setState({ history: JSON.parse(JSON.stringify(rows)) });
    }


    render() {
        return (
            <div className="col-xs-12">
                <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">速食樂透</h3>
                    </div>
                    <div className="box-body">
                        遊戲規則:
                        <ul>
                            <li>每下注一次，即刻知道中獎或沒中。假如中獎，獎金池中的{this.state.rate}%歸您，{100 - this.state.rate - this.state.devRate}%做為下次遊戲的獎金池，剩下{this.state.devRate}%給開發者；假如沒中，您投入的ETH會放入獎金池中，增加獎金池數量。目前獎金池累計:{Math.round(this.state.balance * 1000) / 1000}ETH</li>
                            <li>你所下注的金額，至少0.01ETH為一單位。且中獎機率為一單位的倍數增加，如果下注1ETH中獎機率為0.01ETH的100倍。目前中獎機率為 {this.state.probability && Math.round(1 / this.state.probability * 100000) / 1000}%/單位</li>
                            <li>為了使遊戲生生不息：中獎機率隨時間指數成長，遊戲開始後每經過一天，7天後中獎機率提高1倍，30天後中獎機率為原本的18倍。遊戲開始後已經經過:{this.state.gameStartTime && Math.round((Date.now() / 1000 - this.state.gameStartTime) / 60 / 60 * 100) / 100}小時</li>
                        </ul>
                        <div className="from-group">
                            <label className="control-label">下注</label>
                            <input
                                className="form-control"
                                placeholder="ETH"
                                type="number"
                                onChange={(e) => { this.state.eth = e.target.value }} />
                        </div>
                    </div>
                    <div className="box-footer">
                        {this.props.ethereumStore.address ? <button type="button" className="btn btn-primary" onClick={this.play}>送出</button> : <button className="btn btn-primary" disabled>請從右上角先進行登入</button>}
                    </div>
                </div>
                {/* {this.props.ethereumStore.address === this.state.getDeveloperAddress &&
                    [<div key="1" className="box box-primary">
                        <div className="box-header with-border">
                            <h3 className="box-title">開牌與派彩</h3>
                        </div>
                        <div className="box-body">
                            <button type="button" className="btn btn-primary" onClick={this.finishBtn}>送出</button>
                        </div>
                        <div className="box-footer">

                        </div>
                    </div>,

                    <div key="2" className="box box-primary">
                        <div className="box-header with-border">
                            <h3 className="box-title">設置</h3>
                        </div>
                        <div className="box-body">
                            <div className="form-group">
                                <div>
                                    <label className="control-label">設定競爭條件 競爭人數</label>
                                    <input className="form-control" type="number" onChange={(e) => this.contenders = e.target.value} />
                                    <label className="control-label">投注價格(eth)</label>
                                    <input className="form-control" type="number" onChange={(e) => this.bettingprice = e.target.value} />
                                </div>
                            </div>
                        </div>
                        <div className="box-footer">
                            <button className="btn btn-primary" onClick={this.setBettingConditionBtn}>設定</button>
                        </div>
                    </div>]

                } */}
                {/*<divr" style={{ display: "none" }}>
                    <div>下注賭金<inputr" />(請依右上角之投注金額下注)</div>
                    <div>輸入猜測值
                        <inpute" />
                        <button">下注</button>交易編號:<span">
                        </span>
                    </div>
                </div>
                <divr" style={{ display: "none" }}>
                    <div>輸入一個目標值找最接近的贏家
                        <inpute" />
                        <button">尋找</button>
                        回傳:
                        <span</span>
                    </div>
                    <br />
                    <div>設定競爭條件 競爭人數:
                        <inputs" /> 投注價格(eth):
                        <inpute" />
                        <button">設定</button>
                        交易編號:
                        <span</span>
                    </div>
                    <br />
                    <div>開牌與派彩
                        <button">點擊</button>
                        回傳:
                        <span</span>
                    </div>
                    <br />
                </div> */}
                <div key="1" className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">歷史紀錄</h3>
                    </div>
                    <div className="box-body">
                        <button className="btn btn-primary" onClick={() => {
                            this.state.openHistory || this.setState({ openHistory: true })
                        }}>查詢歷史賭局結果</button>
                        {this.getHistoryReact()}
                    </div>
                </div>
            </div>
        )
    }




}
export { WinOrLost };